import React from 'react'
import {
    CButton,
    CCard,
    CCardBody,
    CCol,  
  } from '@coreui/react'
import CIcon from '@coreui/icons-react';
import { imgPath } from 'src/CommonRoutes/Api';

export default function OrderCard(props) {
    return (
      <>
      {props.data.map((item) => (
            <CCard className="mx-10" style={{ boxShadow: "0px 0px 1px 1px #7706AF", borderRadius: "20px" }}>
            
              <CCardBody className="p-10" >
                <div className="Ordercard_body">
                  <img src={imgPath+"Invoice_pics/"+item.invoiceImage} alt=""/>
                  <p>OrderID : {item.invoice_no}</p>
                </div>
                <p style={{ marginBottom: "0px" }}>Bill Amount: {item.invoiceAmount}</p>
                <p style={{ marginBottom: "0px" }}><CIcon name="cilNotes" /> &nbsp;Bakery</p>
                <p style={{ marginBottom: "0px" }}><CIcon name="cilCalendar" />&nbsp; 22/05/2021</p><br />
                <CCol xs="12" md="12" lg="12" className="content-center" style={{ paddingLeft: "1rem", fontWeight: 'bold' }}>
                  <CButton onClick={(e) => props.handleStatus(item.invoice_no,item.id,e.target.value)} value="accept" color="success" className="addWalletbtn" style={{
                    width: "16rem",
                    height: "2rem",
                    borderBottomLeftRadius: "1rem",
                    borderTopLeftRadius: "1rem",
                    marginLeft: "-1.3rem",
                  }}>Accept</CButton>
                  <CButton onClick={(e) => props.openModal(item.invoice_no,item.id,e.target.value)} value="reject" color="danger" className="addWalletbtn" style={{
                    width: "16rem",
                    height: "2rem",
                    borderBottomRightRadius: "1rem",
                    borderTopRightRadius: "1rem",
                    marginLeft: "0.3rem"
                  }}>Reject</CButton>

                </CCol>


              </CCardBody>
             
            </CCard>
             ))}
             </>
    )
}
