import React, { Component } from 'react';
import {
  CCol,
  CContainer,
  CRow,
  CButton,
    CCard,
    CCardBody,
    CInput,
    CInputGroup,
    CInputGroupPrepend,
    CInputGroupText,
} from '@coreui/react'
import CIcon from '@coreui/icons-react';
import '../../scss/order.scss'
import "src/scss/profile.scss"
import { getOrder } from '../../CommonRoutes/Constant'
import { Base_URL, headers } from '../../CommonRoutes/Api';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import OrderCard from '../vendorComponent/OrderCard';
import Modal from 'react-modal';
import BlockUI from 'src/CommonRoutes/BlockUI/BlockUI';

const customStyles = {
  content : {
    backgroundColor       : 'white',
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)'
  }
};

const __DEV__ = document.domain === 'localhost'
class Order extends Component {
  constructor(props) {
    super(props);
    this.state = {
      orderList: [],
      status: '',
      option: "",
      remark:'',
      modalIsOpen: false,
      submitted: false,
      id:'',
      invoice_no:''
    }
  }

  handleStatus = async (invoice_no,invoice_id,status) => {
    // this.setState({status:e})
      let remark= this.state.remark;
      let url = Base_URL + "accept_order";
      let data = {status,remark,invoice_id,invoice_no};
      const result = await fetch(url, { method: 'POST', headers, body: JSON.stringify(data)
        })
        result.json().then((resp) => {
          console.log(resp);
          console.log(result)
          if(result.status === 402 ){
            console.log(resp.data.required_amount)
            this.displayRazorpay(resp.data.required_amount);
          }else{
            this.componentDidMount();
          }
        },(error) => console.log(error))
  }

  openModal=(invoice_no,id,status)=> {
      this.setState({
        submitted: true,modalIsOpen: true,status:status,id:id,invoice_no:invoice_no
      });
  }

  handleStatusR(){
    let remark= this.state.remark;
    let id=this.state.id;
    let status=this.state.status;
    let invoice_no=this.state.invoice_no;
      let url = Base_URL + "accept_order";
    let data = {status,remark,id,invoice_no};
      fetch(url, { method: 'POST', headers, body: JSON.stringify(data)
        }).then((result) => {
        result.json().then(() => {
          this.setState({modalIsOpen: false})
          this.componentDidMount();
        },(error) => console.log(error))
      
        })
  }

  componentDidMount() {
    this.setState({blocking:true})
    let option="pending"
    getOrder(option).then((orderList) => {
      this.setState({ orderList ,blocking:false});
      console.log(orderList)
    });
  }
  handleChangeoption=(e)=>{
    this.setState({blocking:true})
      getOrder(e).then((orderList) => {
      this.setState({ orderList,blocking:false });
    });
  }
  handleChange=(e)=>{
    this.setState({option:e})
    this.handleChangeoption(e);
  }
  render() {
    return (
<>
      <Modal
          isOpen={this.state.modalIsOpen}
          style={customStyles}
          contentLabel="Example Modal"
        >

{/* <CContainer> */}
            <CRow className="justify-content-center">
                <CCol md="12" lg="12" xl="12">
                    <CCard className="mx-4">
                        <CCardBody className="p-4">
                        <h3>Reject</h3>
  
                          <label>Remark</label>
                            <CInputGroup className="mb-3" >
                            <CInputGroupPrepend>
                              <CInputGroupText>
                                <CIcon name="cil-user" />
                              </CInputGroupText>
                            </CInputGroupPrepend>
                            <CInput type="text" name="dname" placeholder="Remark...." autoComplete="false"
                                    onChange={(event) =>{this.setState({remark:event.target.value})}}    />
                            </CInputGroup>
                            <CButton  type="submit" value="add" block color="primary" fullWidth={true} onClick={()=>this.handleStatusR()}>Submit</CButton>
                        </CCardBody>
                    </CCard>
                </CCol>
            </CRow>
            {/* </CContainer> */}
        </Modal>

      <CContainer style={{ padding: "10px" }}>
          <CCol md="12" lg="12" xl="12" style={{marginTop:'-1rem',marginBottom:'2rem'}}>
       <FormControl style={{width:'100%'}}>
        <InputLabel id="demo-simple-select-label">Select Status</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          // value={this.state.option}
          onChange={(e)=>this.handleChange(e.target.value)}
        >
          <MenuItem value="pending">Pending</MenuItem>
          <MenuItem value="accept">Accepted</MenuItem>
          <MenuItem value="reject">Rejected</MenuItem>
        </Select>
      </FormControl>
      </CCol>

        <CRow className="justify-content-center">
          <CCol md="12" lg="12" xl="12" style={{ padding: "0px" }}>
          {this.state.orderList.data && this.state.orderList.data.length > 0 ? (
              <OrderCard data={this.state.orderList.data} handleStatus={this.handleStatus} openModal={this.openModal} option={this.state.option} />
            ) : (
              "No data found"
            )}
          </CCol>
        </CRow>
      </CContainer>
      <BlockUI blocking={this.state.blocking} />
      </>

    );
  }

  loadScript(src) {
    return new Promise((resolve) => {
      const script = document.createElement('script')
      script.src = src
      script.onload = () => {
        resolve(true)
      }
      script.onerror = () => {
        resolve(false)
      }
      document.body.appendChild(script)
    })
  }

  	async displayRazorpay(amt) {
		const res =  await this.loadScript('https://checkout.razorpay.com/v1/checkout.js')

		if (!res) {
			alert('Razorpay SDK failed to load. Are you online?')
			return
		}

		const options = {
			key: __DEV__ ? 'rzp_test_wOGiV1gpEDUQrb' : 'PRODUCTION_KEY',
      currency: "INR",
      amount:Number(amt+'00'),

			image: 'https://scontent.famd4-1.fna.fbcdn.net/v/t1.6435-9/118735517_106259321210134_6609537456866208869_n.jpg?_nc_cat=108&ccb=1-3&_nc_sid=973b4a&_nc_ohc=OIXJOQyvfYAAX8g-uYI&_nc_ht=scontent.famd4-1.fna&oh=32147705a68745ef4c204efc1eb9c97e&oe=60D201C5',
			handler: function (response) {
        let url = Base_URL + "accept_order";
        let transaction_id=response.razorpay_payment_id;
        let vendor_id=localStorage.getItem("user_id");
        let amount=Number(amt);
        let data={transaction_id,vendor_id,amount}
        fetch(url, { method: 'POST', headers, body: JSON.stringify(data)
        }).then((result) => {
        result.json().then(() => {
        },(error) => console.log(error))
      
        })
			},
			prefill: {
				email: 'sdfdsjfh2@ndsfdf.com',
				phone_number: '9899999999'
			}
		}
		const paymentObject = new window.Razorpay(options)
		paymentObject.open()
	}
}

export default Order;